body {
  font-family: sans-serif;
  padding: 0;
  margin: 0;
  color: #2f3542;
}

.wrapper-container {
}

h2 {
  text-align: left;
  font-size: 2em;
  margin: 40px 20px;
}

.list-container {
  margin-bottom: 50px;
}

.row-container {
  height: 50px;
  line-height: 50px;
  text-align: left;
  color: #2f3542;
  white-space: nowrap;
  padding: 0 20px;
}

.row-container:hover {
  background-color: #f1f2f6;
}

.row-container .row-border {
  width: 95%;
  margin: auto;
  border-bottom: 1px solid #dfe4ea;
}

.cards-container {
  margin: 0 50px 50px;
  text-align: left;
}

.card-container {
  display: inline-block;
  width: 200px;
  height: 150px;
  vertical-align: top;
  text-align: center;
  color: #2f3542;
  margin: 20px 30px;
  border: 2px solid #dfe4ea;
}

.card-title {
  font-size: 1em;
  text-transform: uppercase;
  color: white;
  background-color: #dfe4ea;
  line-height: 30px;
}

.card-content {
  padding: 10px;
  height: 100px;
}

.card-subtitle {
}



